@import url(https://fonts.googleapis.com/css2?family=Open+Sans:wght@300;700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Open+Sans:wght@300;700&display=swap);
:root {
    /* Colors: */
    background-color: #172C49;
    --background-color: #172C49;
    --unnamed-color-ffffff: #FFFFFF;
    --accent-color: #EBECEF;
    /* Font/text values */
    --font-family-open-sans: 'Open Sans', sans-serif;
    font-family: 'Open Sans', sans-serif;
    font-family: var(--font-family-open-sans);
    --unnamed-font-style-normal: normal;
    --unnamed-font-weight-300: 300px;
    --unnamed-font-weight-bold: bold;
    --unnamed-font-size-19: 19px;
    --unnamed-font-size-21: 21px;
    --unnamed-font-size-25: 25pt;
    --unnamed-character-spacing-0: 0px;
    --unnamed-line-spacing-17: 17px;
    --unnamed-line-spacing-28: 28px;
    --unnamed-line-spacing-34: 34px;
    position: relative;
}

.layout-class {
    padding: 10;
    margin: 0;
    width: 100%;
}

.content-area {
    background-color: #EBECEF;
    background-color: var(--accent-color);
    border-top: 10vh solid #172C49;
    border-top: 10vh solid var(--background-color);
    border-left: 5vw solid #172C49;
    border-left: 5vw solid var(--background-color);
    padding-top: 3%;
}

/* Character Styles */

.character-style-1 {
    font-family: var(--unnamed-font-family-open-sans);
    font-style: normal;
    font-style: var(--unnamed-font-style-normal);
    font-weight: bold;
    font-weight: var(--unnamed-font-weight-bold);
    font-size: 25pt;
    font-size: var(--unnamed-font-size-25);
    line-height: 17px;
    line-height: var(--unnamed-line-spacing-17);
    letter-spacing: 0px;
    letter-spacing: var(--unnamed-character-spacing-0);
    color: var(--unnamed-color-172c49);
}

.unnamed-character-style-2 {
    font-family: var(--unnamed-font-family-open-sans);
    font-style: normal;
    font-style: var(--unnamed-font-style-normal);
    font-weight: bold;
    font-weight: var(--unnamed-font-weight-bold);
    font-size: 21px;
    font-size: var(--unnamed-font-size-21);
    line-height: 28px;
    line-height: var(--unnamed-line-spacing-28);
    letter-spacing: 0px;
    letter-spacing: var(--unnamed-character-spacing-0);
    color: var(--unnamed-color-172c49);
}

.character-style-3 {
    font-family: var(--unnamed-font-family-open-sans);
    font-style: normal;
    font-style: var(--unnamed-font-style-normal);
    font-weight: bold;
    font-weight: var(--unnamed-font-weight-bold);
    font-size: 25pt;
    font-size: var(--unnamed-font-size-25);
    line-height: 34px;
    line-height: var(--unnamed-line-spacing-34);
    letter-spacing: 0px;
    letter-spacing: var(--unnamed-character-spacing-0);
    color: #FFFFFF;
    color: var(--unnamed-color-ffffff);
}

.unnamed-character-style-4 {
    font-family: var(--unnamed-font-family-open-sans);
    font-style: normal;
    font-style: var(--unnamed-font-style-normal);
    font-weight: 300px;
    font-weight: var(--unnamed-font-weight-300);
    font-size: 19px;
    font-size: var(--unnamed-font-size-19);
    line-height: 17px;
    line-height: var(--unnamed-line-spacing-17);
    letter-spacing: 0px;
    letter-spacing: var(--unnamed-character-spacing-0);
    color: var(--unnamed-color-172c49);
}

.unnamed-character-style-5 {
    font-family: var(--unnamed-font-family-open-sans);
    font-style: normal;
    font-style: var(--unnamed-font-style-normal);
    font-weight: 300px;
    font-weight: var(--unnamed-font-weight-300);
    font-size: 21px;
    font-size: var(--unnamed-font-size-21);
    line-height: 28px;
    line-height: var(--unnamed-line-spacing-28);
    letter-spacing: 0px;
    letter-spacing: var(--unnamed-character-spacing-0);
    color: #FFFFFF;
    color: var(--unnamed-color-ffffff);
}
* {
  font-family: 'Open Sans', sans-serif;
}

.leaflet-container {
  width: 100%;
  height: 50vh;
}

.App {
  height: 100vh;
}

.card a:hover {
  background-color: grey;
}


